import { React, useState } from 'react';
import { getStorage, ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import html2canvas from 'html2canvas';
import { onAuthStateChanged, getAuth } from 'firebase/auth';
import jsPDF from 'jspdf';
import { useSelector } from 'react-redux';
import firebseSvg from "../../static/assest/svgbutton/firebase-icon.svg"
import { initializeApp } from 'firebase/app';

const firebaseConfig = {
  apiKey: "AIzaSyDZvXshikAs9t45pkktwvxsnHeiAi6Q8qE",
  authDomain: "savepdfdata.firebaseapp.com",
  projectId: "savepdfdata",
  storageBucket: "savepdfdata.appspot.com",
  messagingSenderId: "399053961429",
  appId: "1:399053961429:web:ceb2b665c7fd2bf4f04a5c",
  measurementId: "G-BM85FX6NBQ"
};
const app = initializeApp(firebaseConfig);
const storage = getStorage(app);

function PdfUploader() {
  const user = useSelector((state) => state.user);
  // const userx = useSelector((state) => state.pdf);
  const [uploadedDone, setuploadedDone] = useState(false)
  const auth = getAuth();

  const uploadPdfToFirebase = async (imageData) => {
    if (user.userName) {
      // Generate a unique key (e.g., timestamp)
      const imageContainer = document.getElementById('image-container');
      const originalImage = document.getElementById('original-image');

      html2canvas(imageContainer).then(async (canvas) => {
        const imageDataUri = canvas.toDataURL('image/png');
        const pdf = new jsPDF({
          orientation: 'landscape',
          unit: 'px',
          format: [originalImage.width, originalImage.height],
        });
        pdf.addImage(imageDataUri, 'PNG', 0, 0, originalImage.width, originalImage.height);
        const pdfDataUri = pdf.output('datauristring');

        // Upload PDF to Firebase Storage
        const pdfBlob = await fetch(pdfDataUri).then((res) => res.blob());
        const uniqueKey = `${Date.now()}_${user.userName.uid}`;
        const userPdfRef = ref(storage, `user-pdfs/${user.userName.uid}/${uniqueKey}.pdf`);
        const metadata = {
          contentType: 'application/pdf',
        };
        await uploadBytes(userPdfRef, pdfBlob, metadata);
        const downloadUrl = await getDownloadURL(userPdfRef);
        setuploadedDone(true)
        console.log('PDF uploaded and can be accessed at:', downloadUrl);
      });

    };
  }

    onAuthStateChanged(auth, (users) => {
      if (users) {
       console.log("user IS login In")
      } else {
     setuploadedDone(false)
      }
    });


  return (
    <div className='firebaseButton'>
      {user.isLoggedIn ? "" : <button onClick={uploadPdfToFirebase}>Save in Our DB<img width={15} src={firebseSvg} alt='firebase Icon' /></button>}
      {uploadedDone && <p>Your PDF file has been uploaded to our Firebase.</p>}

    </div>
  );
}

export default PdfUploader;