const initialState = {
    pdfData: "null",
  };
  
  const pdfReducer = (state = initialState, action) => {
    switch (action.type) {
      case 'SET_PDF_DATA': 
        return {
          ...state,
          pdfData: action.payload,
        }; 
      default:
        return state;
    }
  };
 

  export default pdfReducer;