import React, { useEffect, useState } from 'react';
import { getStorage, ref, listAll, getDownloadURL } from 'firebase/storage';
import { useSelector } from 'react-redux';
import "../../styles/userdata.css"
function UserDataFetch() {
  const [userPDFs, setUserPDFs] = useState([]);
  const [loading, setloading] = useState(true)
  const storage = getStorage();
  const user = useSelector((state) => state.user);
  useEffect(() => {
    const fetchUserPDFs = async () => {
      if (user.userName) {
    console.log(user)
        const userPdfRef = ref(storage, `user-pdfs/${user.userName.uid}`);
        const userPdfList = await listAll(userPdfRef);
        const pdfPromises = userPdfList.items.map((item) => getDownloadURL(item));
        console.log(pdfPromises)
        Promise.all(pdfPromises)
          .then((downloadUrls) => {
            setUserPDFs(downloadUrls);
            setloading(false)
          })
          .catch((error) => {
            console.error('Error fetching PDFs:', error);
          });
      }
    };
   
    fetchUserPDFs();
  
  }, [user.userName, storage]);

  return (
<>
{user.isLoggedIn ?"": <div className='dataShowForUser'>
      <h2>User's Saved PDFs:</h2>
    {loading? <p>We're fetching your saved pdf in our Data Base</p>   :
      userPDFs.length > 0 ? (
        <ul>
          {userPDFs.map((pdfUrl, index) => (
            <li key={index}>
              <a href={pdfUrl} target="_blank" rel="noopener noreferrer" download>
                PDF {index + 1}
              </a>
            </li>
          ))}
        </ul>
      ) : (
        <p>No saved PDFs found.</p>
      )
  }
    </div>}

</>
  );
}

export default UserDataFetch;
